import React,{Fragment} from 'react'
import PropTypes from 'prop-types'
import {Link} from 'react-router-dom'
import Moment from 'react-moment' 
import {connect}from 'react-redux'
import {addLike,removeLike,deletePost} from '../../actions/post'
//because we have eventual actions like add like remove like
//
const PostItem =( {
    addLike,
    removeLike,
    deletePost,
    auth,
    post:{ _id,text, firstname, avatar, user,likes, comments,date },
    showActions} )=> {
    
    return (
        <div className="post bg-white p-1 my-1">
        <div>
          <Link to={`profile/${user}`} >
            <img
              className="round-img"
              src={avatar}
              alt=""
            />
            <h4>{firstname}</h4>
          </Link>
        </div>
        <div>
          <p className="my-1">{text}    </p>
           <p className="post-date">
           Posted on <Moment format="YYY/MM/DD">  {date}</Moment> 
          </p>

      {showActions && (<Fragment>
        <button onClick={e=>addLike(_id)} type="button" className="btn btn-light">
            <i className="fas fa-thumbs-up"/>{' '}
            <span>{ likes.length>0 &&
              (<span >{likes.length}</span>  )
            }</span>
          </button>
          <button  onClick={e=>removeLike(_id)} type="button" className="btn btn-light">
            <i className="fas fa-thumbs-down"></i>
          </button>
          <Link to={`/posts/${_id}`} className="btn btn-primary">
            Discussion{' '} {comments.length>0 &&  (
            <span className='comment-count'>{comments.length}</span>    )
            }
            
             </Link>
    {/*thhis is the post user     and this is the logged user*/}
    {!auth.loading && user    ===      auth.user._id  &&
    (
        <button onClick={e=>deletePost(_id)}  type="button"  className="btn btn-danger" >
            <i className="fas fa-times"></i>
        </button>
   )}</Fragment>)
   
   }


          
          
        </div>
      </div>
    )
}
//definir par defaut de porps
PostItem.defaultProps={
  showActions:true
}

PostItem.propTypes = {
post:PropTypes.object.isRequired,
auth:PropTypes.object.isRequired,
addLike:PropTypes.func.isRequired,
removeLike:PropTypes.func.isRequired,
deletePost:PropTypes.func.isRequired,
}
const MapStateToProps = state => ({
    auth:state.auth
})
export default connect(MapStateToProps,{addLike,removeLike,deletePost})(PostItem)
//we need to state whose whose the delete button 
//shows up (rvler montrer)  only on users where the post belongs to them