import React from 'react'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {Link} from 'react-router-dom'
import Moment from 'react-moment'
import {deleteComment} from '../../actions/post'
const CommentItem = ({
    postId,
    comment:{_id,text,firstname,avatar,user,date},
    auth,
    deleteComment
}) => (
  
        <div className="post bg-white p-1 my-1">
        <div>
          <Link to={`/profile/${user}`}>
            <img
              className="round-img"
              src={avatar}
              alt=""
            />
            <h4>{firstname}</h4>
          </Link>
        </div>
        <div>
          <p className="my-1">   {text}    </p>
           <p className="post-date">
             Posted on <Moment format="YYY/MM/DD">{date}</Moment>
          </p>
          {/* we make sure that the auth is not loading 
          and make sure that the actual user makes the comment
          */}
          { !auth.loading && user === auth.user._id && (
              <button onClick={e=>deleteComment(postId,_id)} type="button" className="btn brn-danger">
                  <i className="fas fa-times"></i>
              </button>
          )}
        </div>
        </div>
   
)

CommentItem.propTypes = {
postId:PropTypes.string.isRequired,
comment:PropTypes.object.isRequired,
auth:PropTypes.object.isRequired,
deleteComment:PropTypes.func.isRequired,

}
const mapStateToProps = state=>({
    auth:state.auth
})

export default connect(mapStateToProps,{deleteComment})(CommentItem)
