//racfp
import React,{Fragment,useState} from 'react'
//with router because redirecting in the action file
import {Link,withRouter} from 'react-router-dom'
import PropTypes from 'prop-types'
import {connect} from 'react-redux'
import {addEducation} from '../../actions/profile'
import 'bootstrap/dist/css/bootstrap.min.css';

const AddEducation = ({addEducation,history}) => {
    const [formData,setFormData]=useState({
        school:'',
        degree:'',
        fieldofstudy:'',
        from:'',
        //I want to have 'to' is disabled if current is true
        to:'',
        current:false,
        description:''
    })
    const [toDateDisabled,toggleDisabled]=useState(false);
    const {school,degree,fieldofstudy,from,to,current,description}=formData;
    const onChange = (e) => {setFormData({...formData,[e.target.name]:e.target.value})};
 
  
    return (
        <Fragment>
          <h1 className="large text-info">
       Add An Education
      </h1>
      <p className=' font-size: 1.5rem,  margin-bottom: 1rem;'>
        <i className="fas fa-code-branch"></i> Add any school or bootcamp that you have attended      </p>
      <small>* = required field</small>
      <form className="form" onSubmit={e=> {
        e.preventDefault();
        addEducation(formData,history)
      }} >
       
        <div className='form-group'>
          <input
            type='text'
            placeholder='* school or bootcamp'
            name='school'
            value={school}
            required
            onChange={(e) => onChange(e)}
          />
        </div>
        <div className="form-group">
          <input type="text" placeholder="* degree or certificate" name="degree" value={degree}   onChange={(e) => onChange(e)} required />
        </div>
        <div className="form-group">
          <input type="text" placeholder="Field of study" name="fieldofstudy" value={fieldofstudy}  onChange={e=>onChange(e)} />
        </div> 
        <div className="form-group">
          <h4>From Date</h4>
          <input type="date" name="from" value={from}  onChange={e=>onChange(e)} />
        </div>
         <div  className=' form-group custom-control custom-checkbox custom-control-inline'>
          <input className='custom-control-input' id='defaultInline1' type="checkbox" name="current" checked={current} value={current}  
          onChange={
              e=>{
                  setFormData({...formData,current:!current});
                  toggleDisabled(!toDateDisabled);
                 }
                 } />
         <label  className='custom-control-label'   htmlFor='defaultInline1'     style={{ marginLeft: 20 }}  > Current School</label>
        </div>
        <div className="form-group">
          <h4>To Date</h4>
          <input type="date" name="to" value={to}  onChange={e=>onChange(e)} disabled={toDateDisabled? 'disabled' :'' } />
        </div>
        <div className="form-group">
          <textarea
            name="description"
            cols="30"
            rows="5"
            placeholder="Program Description"
            value={description}
            onChange={e=>onChange(e)}
          ></textarea>
        </div>
        <input type="submit" className="btn btn-primary my-1" />
        <Link className="btn btn-light my-1" to="/dashboard">Go Back</Link>
      </form>   
        </Fragment>
    )
}

AddEducation.propTypes = {
addEducation:PropTypes.func.isRequired,
}
//we add the action addexpereince to connect 
export default connect(null,{addEducation})(withRouter(AddEducation));
